.Header{height:10vh;background-color:#242424;border-bottom:1px solid whitesmoke;display:flex;align-items:center;justify-content:flex-end}.Header ul{display:flex;list-style:none;margin:0 15%;font-size:2vw}.Header ul li a{color:whitesmoke;margin:0 10px;text-decoration:none}.is-selected{border-bottom:3px solid rgba(145,41,41,0.918)}@media (max-width: 767px){.Header ul{font-size:4vw}}


#presentation{
  width: 480px;
  height: 120px;
  padding: 20px;
  margin: auto;
  background: #FFF;
  margin-top: 10px;
  box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23); 
  transition: all 0.3s; 
  border-radius: 3px;
}

#presentation:hover{
  box-shadow: 0 14px 28px rgba(0,0,0,0.25), 0 10px 10px rgba(0,0,0,0.22);
  transition: all 0.3s;
  transform: translateZ(10px);
}

#floating-button{
  width: 55px;
  height: 55px;
  border-radius: 50%;
  /*background: rgb(37, 172, 250);*/
  position: fixed;
  bottom: 30px;
  right: 30px;
  cursor: pointer;
  box-shadow: 0px 2px 5px #666;
}

.plus{
  color: white;
  position: absolute;
  top: 0;
  display: block;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  padding: 0;
  margin: 0;
  line-height: 55px;
  font-size: 38px;
  font-family: 'Roboto';
  font-weight: 300;
  -webkit-animation: plus-out 0.3s;
          animation: plus-out 0.3s;
  transition: all 0.3s;
}

#container-floating{
  position: fixed;
  width: 70px;
  height: 70px;
  bottom: 30px;
  right: 30px;
  z-index: 50px;
}

#container-floating:hover{
  height: 400px;
  width: 90px;
  padding: 30px;
}

#container-floating:hover .plus{
  -webkit-animation: plus-in 0.15s linear;
          animation: plus-in 0.15s linear;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.edit{
  position: absolute;
  top: 0;
  display: block;
  bottom: 0;
  left: 0;
  display: block;
  right: 0;
  padding: 0;
  opacity: 0;
  margin: auto;
  line-height: 65px;
  transform: rotateZ(-70deg);
  transition: all 0.3s;
  -webkit-animation: edit-out 0.3s;
          animation: edit-out 0.3s;
}

#container-floating:hover .edit{
  -webkit-animation: edit-in 0.2s;
          animation: edit-in 0.2s;
   -webkit-animation-delay: 0.1s;
           animation-delay: 0.1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes edit-in{
    from {opacity: 0; transform: rotateZ(-70deg);}
    to {opacity: 1; transform: rotateZ(0deg);}
}

@keyframes edit-in{
    from {opacity: 0; transform: rotateZ(-70deg);}
    to {opacity: 1; transform: rotateZ(0deg);}
}

@-webkit-keyframes edit-out{
    from {opacity: 1; transform: rotateZ(0deg);}
    to {opacity: 0; transform: rotateZ(-70deg);}
}

@keyframes edit-out{
    from {opacity: 1; transform: rotateZ(0deg);}
    to {opacity: 0; transform: rotateZ(-70deg);}
}

@-webkit-keyframes plus-in{
    from {opacity: 1; transform: rotateZ(0deg);}
    to {opacity: 0; transform: rotateZ(180deg);}
}

@keyframes plus-in{
    from {opacity: 1; transform: rotateZ(0deg);}
    to {opacity: 0; transform: rotateZ(180deg);}
}

@-webkit-keyframes plus-out{
    from {opacity: 0; transform: rotateZ(180deg);}
    to {opacity: 1; transform: rotateZ(0deg);}
}

@keyframes plus-out{
    from {opacity: 0; transform: rotateZ(180deg);}
    to {opacity: 1; transform: rotateZ(0deg);}
}

.nds{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  position: fixed;
  z-index: 300;
  transform:  scale(0);
  cursor: pointer;
}

.nd1{
  right: 40px;
  bottom: 120px;
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
    -webkit-animation: bounce-out-nds 0.3s linear;
            animation: bounce-out-nds 0.3s linear;
  -webkit-animation-fill-mode:  forwards;
          animation-fill-mode:  forwards;
}

.nd3{
  right: 40px;
  bottom: 180px;
  -webkit-animation-delay: 0.15s;
          animation-delay: 0.15s;
    -webkit-animation: bounce-out-nds 0.15s linear;
            animation: bounce-out-nds 0.15s linear;
  -webkit-animation-fill-mode:  forwards;
          animation-fill-mode:  forwards;
}

.nd4{
  right: 40px;
  bottom: 240px;
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
    -webkit-animation: bounce-out-nds 0.1s linear;
            animation: bounce-out-nds 0.1s linear;
  -webkit-animation-fill-mode:  forwards;
          animation-fill-mode:  forwards;
}

.nd5{
  background-size: 100%;
  right: 40px;
  bottom: 300px;
  -webkit-animation-delay: 0.08s;
          animation-delay: 0.08s;
  -webkit-animation: bounce-out-nds 0.1s linear;
          animation: bounce-out-nds 0.1s linear;
  -webkit-animation-fill-mode:  forwards;
          animation-fill-mode:  forwards;
}

@-webkit-keyframes bounce-nds{
    from {opacity: 0;}
    to {opacity: 1; transform: scale(1);}
}

@keyframes bounce-nds{
    from {opacity: 0;}
    to {opacity: 1; transform: scale(1);}
}

@-webkit-keyframes bounce-out-nds{
    from {opacity: 1; transform: scale(1);}
    to {opacity: 0; transform: scale(0);}
}

@keyframes bounce-out-nds{
    from {opacity: 1; transform: scale(1);}
    to {opacity: 0; transform: scale(0);}
}

#container-floating:hover .nds{
  
  -webkit-animation: bounce-nds 0.1s linear;
  
          animation: bounce-nds 0.1s linear;
  -webkit-animation-fill-mode:  forwards;
          animation-fill-mode:  forwards;
}

#container-floating:hover .nd3{
  -webkit-animation-delay: 0.08s;
          animation-delay: 0.08s;
}
#container-floating:hover .nd4{
  -webkit-animation-delay: 0.15s;
          animation-delay: 0.15s;
}
#container-floating:hover .nd5{
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

